.page-footer {
  background-color: $color-black-pearl;
  min-height: 280px;

  @include vp-1023 {
    min-height: 465px;
  }
}

.page-footer__wrap {
  max-width: 1294px;
  padding-top: 50px;
  padding-right: 60px;
  padding-bottom: 35px;
  display: flex;
  justify-content: space-between;

  @include vp-1023 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 9px 30px;
    padding-top: 50px;
  }

  @include vp-767 {
    padding-top: 35px;
    display: flex;
    flex-direction: column;
    gap: 0;
  }
}

.page-footer__social-wrap {
  display: flex;
  flex-direction: column;
  width: 28%;

  @include vp-1023 {
    grid-column: 1/-1;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    margin-bottom: 5px;
  }
}

.page-footer__logo {
  margin-bottom: 24px;

  @include vp-767 {
    margin-bottom: 9px;
  }
}

.page-footer__social-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  @include vp-1023 {
    margin-right: -12px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 8px;
  }
}

.page-footer__social-link {
  display: inline-block;
  vertical-align: middle;
  padding: 6px;

  @include hover-focus {
    .page-footer__social-icon {
      fill: $color-deep-sky-blue;
    }
  }
}

.page-footer__social-item {
  &:not(:last-child) {
    margin-right: 3px;
  }
}

.page-footer__nav-wrap {
  position: relative;

  @include vp-767 {
    &::before {
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(248, 253, 255, 0.7);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
    }

    &:last-child::after {
      content: "";
      width: 100vw;
      height: 1px;
      background-color: rgba(248, 253, 255, 0.7);
      position: absolute;
      bottom: 4px;
      left: 50%;
      transform: translateX(-50%);
    }

    padding: 12px 0;
  }

  &--site {
    display: flex;
    justify-content: flex-start;
    width: 39%;

    .page-footer__nav-link {
      user-select: none;
    }

    @include vp-1023 {
      width: 100%;
      flex-direction: column;
      padding-right: 75px;
    }

    @include vp-767 {
      padding-right: 0;
    }
  }

  &--contacts {
    width: 27%;

    @include vp-1023 {
      width: 100%;
    }
  }
}

.page-footer__list-wrap {
  width: 50%;
  margin-right: 19px;

  @include vp-1023 {
    width: 100%;
    margin: 0;
  }
}

.page-footer__nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;

  &--more {
    padding-top: 46px;

    @include vp-1023 {
      padding-top: 0;
    }
  }
}

.page-footer__nav-item {
  &:not(:last-child) {
    margin-bottom: 8px;

    @include vp-767 {
      margin-bottom: 17px;
    }
  }

  @include vp-1023 {
    margin-bottom: 7px;
  }

  &--tel {
    &:not(:last-child) {
      margin-bottom: 8px;
    }

    @include vp-767 {
      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }

  &--address {
    margin-bottom: 8px;

    @include vp-767 {
      margin-bottom: 17px;
    }
  }
}

.page-footer__nav-title {
  @include font(400, 15px, 16px);
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  margin: 5px 0 25px;
  position: relative;

  @include vp-1023 {
    margin: 5px 0 22px;
  }

  @include vp-767 {
    margin: 0;
    padding: 10px 25px 10px 0;
  }
}

.page-footer__nav-link {
  @include font(400, 16px, 22px);
  font-family: $ff-ptmono;
  color: $color-transparent;

  @include vp-767 {
    font-size: 14px;
    line-height: 25px;
  }

  @include hover-focus {
    color: $color-deep-sky-blue;
  }
}

.page-footer__address {
  @include font(400, 16px, 22px);
  font-family: $ff-ptmono;
  color: $color-transparent;
  margin: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
    margin-top: 8px;
  }
}

.is-active[data-spoiler-toggle] {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 17px;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: $color-silver;
  }

  &::after {
    transform: rotate(90deg);
  }
}

.page-footer__nav-wrap.is-closed .page-footer__nav-list--site,
.page-footer__nav-wrap.is-closed .page-footer__nav-list--contacts {
  display: none;
}

.is-open[data-spoiler-toggle] {
  &::after {
    display: none;
  }
}
