@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmonobold.woff2") format("woff2"), url("../fonts/ptmonobold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.35;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-family: "PT Mono", "Courier New", monospace;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--preload {
  transition: none;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  padding: 35px 40px 45px;
  background-color: #00b3ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.modal__close-button::before, .modal__close-button::after {
  content: "";
  position: absolute;
  top: 19px;
  left: 7px;
  width: 28px;
  height: 1px;
  background-color: #ffffff;
}
.modal__close-button::before {
  transform: rotate(45deg);
}
.modal__close-button::after {
  transform: rotate(-45deg);
}

.modal__title {
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  margin: 0 0 4px;
}

.modal__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0 0 40px;
}

.modal__form-wrap form {
  display: flex;
  flex-direction: column;
}

.modal__form-wrap input:not([type=checkbox]) {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  height: 50px;
  padding: 0 15px;
  margin: 0 0 20px;
}
.modal__form-wrap input:not([type=checkbox])::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.modal__form-wrap input:not([type=checkbox]):focus-visible {
  outline: 1px solid #e5e5e5;
}

.modal__form-wrap textarea {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  height: 155px;
  grid-column: 1/-1;
  padding: 23px 16px;
  margin-bottom: 25px;
}
.modal__form-wrap textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.modal__form-wrap textarea:focus-visible {
  outline: 1px solid #e5e5e5;
}

.modal__form-wrap button {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #041427;
  border-radius: 3px;
  width: 100%;
  height: min-content;
  padding: 14px 10px 12px;
  border: none;
  margin-bottom: 20px;
}
.modal__form-wrap button:disabled {
  background-color: rgba(4, 24, 39, 0.5);
}

.modal__form-wrap span {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  display: block;
  position: relative;
}
.modal__form-wrap span::before {
  content: "";
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
  position: absolute;
  left: -25px;
  top: 1px;
}
@media (max-width: 1023px) {
  .modal__form-wrap span {
    padding: 8px 0 0;
  }
  .modal__form-wrap span::before {
    left: -25px;
    top: 35%;
  }
}
@media (max-width: 767px) {
  .modal__form-wrap span {
    padding: 5px 0 0;
  }
  .modal__form-wrap span::before {
    left: -26px;
    top: 30%;
  }
}

.modal__form-wrap input[type=checkbox]:checked + span::before {
  background-image: url("../img/svg/checkbox-tick.svg");
  background-size: 9px 6px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.modal__form-wrap input[type=checkbox]:focus-visible ~ span::before {
  border-color: #000000;
}

.modal__form-wrap label {
  padding-left: 34px;
  order: 1;
  cursor: pointer;
}

.modal__form-wrap input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  transform: translate(-17px, 10px);
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page-header {
  z-index: 2;
}

.page-header__wrap {
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  display: flex;
}
@media (max-width: 1023px) {
  .page-header__wrap {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .page-header__wrap {
    min-height: 70px;
  }
}

.page-header__wrap.is-sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.page-header__container {
  max-width: 1300px;
  align-items: center;
  display: grid;
  grid-template-columns: 31% 1fr 235px;
  padding-top: 10px;
  padding-bottom: 10px;
}
@media (max-width: 1023px) {
  .page-header__container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    grid-template-columns: 30% 1fr;
  }
}

.page-header__logo {
  padding: 0 0 3px;
}

@media (max-width: 767px) {
  .page-header__logo-image {
    width: 35px;
    height: 35px;
  }
}

.page-header__buttons-wrap {
  padding: 10px 15px;
  display: block;
}
@media (max-width: 1023px) {
  .page-header__buttons-wrap {
    display: none;
  }
}

.page-header__contacts-link {
  position: relative;
}
.page-header__contacts-link--contact {
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  padding: 5px 5px 5px 32px;
}
@media (hover: hover) {
  .page-header__contacts-link--contact:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.page-header__contacts-link--contact.focus-visible:focus {
  outline: 2px solid #00b2ff;
  border-radius: 5px;
}
.page-header__contacts-link--contact.focus-visible:active {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .page-header__contacts-link--contact {
    padding-left: 14px;
  }
}
@media (max-width: 1023px) and (hover: hover) {
  .page-header__contacts-link--contact:hover:not(.focus-visible) {
    opacity: 1;
    outline: none;
  }
}
@media (max-width: 1023px) {
  .page-header__contacts-link--contact.focus-visible:focus {
    opacity: 1;
    outline: none;
  }
}
.page-header__contacts-link--button-call {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #f2f5f7;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
  width: 160px;
  min-height: 40px;
  padding: 8px 5px;
  background-color: transparent;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  margin-bottom: 8px;
  user-select: none;
  display: block;
}
@media (hover: hover) {
  .page-header__contacts-link--button-call:hover:not(.focus-visible) {
    background-color: rgba(0, 178, 255, 0.2);
    border-color: #00b2ff;
  }
}
.page-header__contacts-link--button-call.focus-visible:focus {
  outline: 2px solid #00b2ff;
}
.page-header__contacts-link--button-call.focus-visible:active {
  opacity: 0.8;
}

.page-header__contacts-icon {
  position: absolute;
  left: -3px;
  top: 7px;
}
@media (max-width: 1023px) {
  .page-header__contacts-icon {
    left: -1px;
    top: 6px;
  }
}

.page-header__contacts-list {
  list-style-type: none;
  padding: 0 0 15px;
  margin: 0;
  display: grid;
  grid-template-columns: 40% 60%;
  gap: 8px;
}
@media (max-width: 1023px) {
  .page-header__contacts-list {
    display: flex;
    justify-content: flex-end;
    gap: 0;
    padding: 0 30px;
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .page-header__contacts-list {
    padding: 0 20px;
  }
}

@media (max-width: 1023px) {
  .page-header__contacts-text {
    display: none;
  }
}

@media (max-width: 1023px) {
  .page-header__contacts-item {
    margin-bottom: 23px;
  }
  .page-header__contacts-item:not(:first-child) {
    margin-left: 41px;
  }
}
@media (max-width: 767px) {
  .page-header__contacts-item {
    margin-bottom: 10px;
  }
}

.promo {
  min-height: 880px;
  margin-top: -120px;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  position: relative;
  overflow: hidden;
}
.promo img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  z-index: -1;
}
@media (max-width: 1023px) {
  .promo {
    min-height: 640px;
    margin-top: -100px;
  }
  .promo img {
    object-position: 20% 58%;
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: 560px;
    margin-top: -70px;
  }
  .promo img {
    object-position: 26% 58%;
  }
}

.promo__wrap {
  width: 1006px;
  padding: 305px 28px 10px;
}
@media (max-width: 1023px) {
  .promo__wrap {
    width: 670px;
    padding: 180px 0 10px;
  }
}
@media (max-width: 767px) {
  .promo__wrap {
    width: 100%;
    padding-top: 150px;
  }
}

.promo__title {
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
  margin: 0 0 31px;
}
@media (max-width: 1023px) {
  .promo__title {
    font-size: 64px;
    line-height: 75px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .promo__title {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 8px;
  }
}

.promo__description {
  font-family: "PT Mono", "Courier New", monospace;
