  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  margin: 0 0 12px;
}
@media (max-width: 1023px) {
  .feedback__title {
    margin: 0 0 6px;
  }
}
@media (max-width: 767px) {
  .feedback__title {
    font-size: 30px;
    line-height: 42px;
  }
}

.feedback__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0 0 34px;
}
@media (max-width: 1023px) {
  .feedback__subtitle {
    margin: 0 0 26px;
  }
}
@media (max-width: 767px) {
  .feedback__subtitle {
    font-size: 14px;
    line-height: 19px;
    margin: 0 0 35px;
  }
}

.feedback__form-wrap form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 20px;
  align-items: baseline;
}
@media (max-width: 1023px) {
  .feedback__form-wrap form {
    align-items: center;
  }
}
@media (max-width: 767px) {
  .feedback__form-wrap form {
    grid-template-columns: 1fr;
  }
}

.feedback__form-wrap input:not([type=checkbox]) {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  height: 50px;
  padding: 0 15px;
  width: 100%;
}
.feedback__form-wrap input:not([type=checkbox])::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.feedback__form-wrap input:not([type=checkbox]):focus-visible {
  outline: 1px solid #e5e5e5;
}

.feedback__form-wrap textarea {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  height: 155px;
  grid-column: 1/-1;
  padding: 23px 16px;
  margin-bottom: 14px;
}
.feedback__form-wrap textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
}
.feedback__form-wrap textarea:focus-visible {
  outline: 1px solid #e5e5e5;
}
@media (max-width: 1023px) {
  .feedback__form-wrap textarea {
    padding: 30px 15px 10px;
  }
}
@media (max-width: 767px) {
  .feedback__form-wrap textarea {
    padding: 23px 15px 10px;
    height: 180px;
    margin-bottom: 10px;
  }
}

.feedback__form-wrap button {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #041427;
  border-radius: 3px;
  width: 97%;
  height: min-content;
  padding: 15px 10px 12px;
  border: none;
  cursor: pointer;
  user-select: none;
}
@media (max-width: 1023px) {
  .feedback__form-wrap button {
    width: 83%;
    padding: 17px 10px 9px;
  }
}
@media (max-width: 767px) {
  .feedback__form-wrap button {
    width: 100%;
    padding: 15px 10px 11px;
  }
}
@media (hover: hover) {
  .feedback__form-wrap button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.feedback__form-wrap button.focus-visible:focus {
  opacity: 0.8;
}
.feedback__form-wrap button:disabled {
  background-color: #808080;
}

.feedback__form-wrap span {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  display: block;
  position: relative;
  cursor: pointer;
}
.feedback__form-wrap span::before {
  content: "";
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
  position: absolute;
  left: -26px;
  top: 6px;
}
@media (max-width: 1023px) {
  .feedback__form-wrap span {
    padding: 8px 0 0;
  }
  .feedback__form-wrap span::before {
    left: -25px;
    top: 35%;
  }
}
@media (max-width: 767px) {
  .feedback__form-wrap span {
    padding: 5px 0 0;
  }
  .feedback__form-wrap span::before {
    left: -26px;
    top: 30%;
  }
}

.feedback__form-wrap input[type=checkbox]:checked + span::before {
  background-image: url("../img/svg/checkbox-tick.svg");
  background-size: 9px 6px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.feedback__form-wrap input[type=checkbox]:focus-visible ~ span::before {
  border-color: #000000;
}

.feedback__form-wrap label {
  padding-left: 37px;
  order: 1;
}
@media (max-width: 1023px) {
  .feedback__form-wrap label {
    padding-left: 25px;
  }
}
@media (max-width: 767px) {
  .feedback__form-wrap label {
    padding-left: 31px;
  }
}

.feedback__form-wrap input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  transform: translate(-18px, 14px);
}

.feedback__field-wrap {
  position: relative;
}